import React from "react";
import { StaticQuery, graphql, Link } from "gatsby";
import "../scss/main.scss";

export default () => (
  <StaticQuery
    query={graphql`
      query {
        site {
          siteMetadata {
            title
            name
          }
        }
      }
    `}
    render={({ site: { siteMetadata: info } }) => (
      <div className="header">
        <Link to="/">
          <h1>{info.name}</h1>
          {/* <img
            src={`https://res.cloudinary.com/chibipicks/assets/logo.png`}
            alt={info.name}
          /> */}
        </Link>
      </div>
    )}
  />
);
