import React from "react";
import Layout from "../components/Layout";
import Image from "../components/Image";
import { Helmet } from "react-helmet";

import { graphql, Link } from "gatsby";

const Products = ({ products }) =>
  products.map(({ node }) => (
    <div className="product" key={node.id}>
      <Link to={node.slug}>
        <Image src={node.imgs[0]} alt={node.name} />
      </Link>
      {/* <div>{node.sizes.toString()}</div> */}
      <div className="product-details">
        <div className="product-name">{node.name}</div>
        <div className="product-price">
        <span className="origin-price">{(node.price).toFixed(2)} EGP</span><span>{(node.price - Math.floor(node.price * node.discount / 100)).toFixed(2)} EGP</span></div>
        {/* <Link to={node.slug} className="details-btn btn-primary">
          Details
        </Link> */}
      </div>
    </div>
  ));

export default ({ data }) => (
  <Layout>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Chibi Picks</title>
      <meta name="viewport" content="width=device-width, initial-scale=1" />
      <link rel="stylesheet" href="https://use.typekit.net/zgs7wbs.css"/>
    </Helmet>
    <Products products={data.allProductsJson.edges} />
  </Layout>
);

export const query = graphql`
  query {
    allProductsJson {
      edges {
        node {
          id
          slug
          name
          imgs
          sizes
          price
          discount
        }
      }
    }
  }
`;
